.app-list {
  margin-top: 10px;
  overflow-y: scroll;
  height: 330px;
  transition: all 0.3s ease;
}
.app-list__complited {
  display: block;
}
.app-list-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
::-webkit-scrollbar {
  width: 10px;
  transition: all 0.3s ease;
}
::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 6px;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #5c5c5c;
}
